import { useEffect, useRef, useState } from 'react'

export const useUpdate = () => {
  const [,setU] = useState(0)
  const update = useRef(() => {setU(u => u+1)})
  return update.current
}

export const useImmobilization = (value, fn = false) => {
  
  const ref = useRef(null)
  const getRef = () => {
    if(ref.current === null) {
      if(fn) {
        ref.current = value()
      } else {
        ref.current = value
      }
    }
    return ref.current
  }
  return getRef()
}

export const useSyncState = (value) => {
  const [v, setV] = useState(value)
  useEffect(() => {
    if(v !== value) {
      setV(value)
    }
  },[v, value])
  return v
}

export const useInterval = (interval, fn) => {
  const func = useRef(fn)
  const intervalRef = useRef(interval)

  useEffect(() => {
    const id = setInterval(() => {
      func.current()
    },intervalRef.current)
    
    return () => clearInterval(id)
  },[])
}

class Subscribeable{
  #val
  #listeners = new Set()

  constructor(value) {
    this.#val = value
  }

  get() {
    return this.#val
  }

  set(value) {
    let val = value
    if(typeof value === 'function') {
      val = value(this.#val)
    }
    if(this.#val !== val) {
      this.#val = val
      for(let linstener of this.#listeners) {
        linstener(this.#val)
      }
    }
  }

  subscribe(fn){
    this.#listeners.add(fn)
  }

  unsubscribe(fn) {
    this.#listeners.delete(fn)
  }
}

export const useSubscribe = (v) => {
  return useImmobilization(() => {
    return new Subscribeable(v)
  }, true)
}
