import { createContext, useContext, useEffect, useRef, useState } from "react"
import { g } from '../../utils/Function'
import { useImmobilization, useUpdate } from '../../utils/Hooks'
import { Resizeable } from '../../utils/Resizeable'
import Sort from '../../utils/Sort'
import { ImmobilizationContext, ProcessContext } from '../../Windows11'
import './Taskmgr.sass'

const randomFixed1 = max => {
  const random = parseFloat(((Math.random()*max) - (Math.random()*max*0.3)).toFixed(1))
  return random > 0 ? random : 0
}

const SelectPidContext = createContext()
const SortContext = createContext();

const Taskmgr = props => {

  const [selectedPid, setSelectedPid] = useState(undefined);

  const [sort, setSort] = useState({
    type:'name',
    asc:true
  })

  const update = useUpdate()

  const getSort = useImmobilization((type, asc) => {
    return new Sort((v1,v2) => {
      return asc ^ (v1[type] > v2[type]);
    })
  })

  //const normal = getSort(sort.type,sort.asc);
  //const system = getSort(sort.type,sort.asc);


  const {apps, close} = useContext(ImmobilizationContext)
  const process = useContext(ProcessContext)

  const gcpuRef = useRef()
  const gmemRef = useRef()

  const [rows, setRows] = useState(getSort(sort.type, sort.asc))

  const mockUpdate = useImmobilization((selectedPid, sort, process) => {
    const rows = getSort(sort.type, sort.asc)
    gcpuRef.current = 0
    gmemRef.current = 0

    let mockGcpu = Math.random() * 50 + 20
    let mockGmem = Math.random() * 7000 + 2200

    let selectedPidLive = false
  
    for(let [pid, aid] of Object.entries(process)) {
      if(pid === selectedPid) {
        selectedPidLive = true
      }

      const cpu = randomFixed1(mockGcpu)
      const mem = randomFixed1(mockGmem)
      mockGcpu -= cpu
      mockGmem -= mem
      gcpuRef.current += cpu
      gmemRef.current += mem
      const app = apps[aid]
      const row = {
        pid:pid,
        icon:app.getIcon(),
        cpu:cpu,
        mem:mem,
        name: app.window.title
      }
      rows.insert(row)
    }
    gcpuRef.current = gcpuRef.current.toFixed()
    gmemRef.current = (gmemRef.current/16384*100).toFixed()
    if(!selectedPidLive) {
      setSelectedPid(undefined)
    }

    setRows(rows)
  })

  useEffect(() => {
    //rowsRef.current = getSort(sort.type, sort.asc)
    //mock()
    mockUpdate(selectedPid,sort, process)

    const intervalId = setInterval(() => {
      mockUpdate(selectedPid,sort,process)
    },1000)
    return () => clearInterval(intervalId)
  },[selectedPid,mockUpdate,sort,process])

  const onClick = e=>{
    if(e.taskmgrSelectedPid !== selectedPid) {
      setSelectedPid(e.taskmgrSelectedPid);
    }
  }

  const switchSort = (type) => {
    if(type === sort.type) {
      setSort({
        type,
        asc: !sort.asc
      })
    } else {
      setSort({
        type,
        asc: true
      })
    }
  }

  const closeProcess = () => {
    if(selectedPid !== undefined) {
      close(selectedPid)
    }
  }

  return (
    <SortContext.Provider value={sort}>
    <SelectPidContext.Provider value={selectedPid}>
    <div className='taskmgr' onClick={onClick}>
      <Main rows={[...rows]} switchSort={switchSort} gcpu={gcpuRef.current} gmem={gmemRef.current}/>
      <Footer update={update} selectedPid={selectedPid} closeProcess={closeProcess}/>
    </div>
    </SelectPidContext.Provider>
    </SortContext.Provider>
  )
}

const Footer = props => {
  const {selectedPid, closeProcess} = props

  return <div className="footer">
    <input type='button' className="close-process-btn" disabled={selectedPid===undefined} onClick={closeProcess} value='结束任务'/>
  </div>
}

const Main = props => {
  const [idS,setIdS] = useState({width:300});
  const [cpuS,setCpuS] = useState({width:100});
  const [memS,setMemS] = useState({width:100});

  const [wrapWidth, setWrapWidth] = useState()
  const [wrapHeight, setWrapHeight] =useState()
  const [contentHeight, setContentHeight] =useState()

  const wrapRef = useRef()
  const contentRef = useRef()
  const contentWidth = idS.width+cpuS.width+memS.width

  useEffect(() => {
    const wrap = wrapRef.current
    const content = contentRef.current

    const wrapObserver = new ResizeObserver(() => {
      const wrapRect = wrap.getBoundingClientRect()
      const newWrapWidth = wrapRect.width
      setWrapWidth(newWrapWidth)
      const newWrapHeight = wrapRect.height
      setWrapHeight(newWrapHeight)
    })

    const contentObserver = new ResizeObserver(() => {
      setContentHeight(content.offsetHeight)
    })

    wrapObserver.observe(wrap)
    contentObserver.observe(content)

    return () => {
      wrapObserver.unobserve(wrap)
      contentObserver.unobserve(content)
    }
  },[])


  const wrapStyle = {}

  if(g.notUndefined(contentWidth) && contentWidth > wrapWidth) {
    wrapStyle.overflowX = 'scroll'
  }

  if(g.notUndefined(wrapHeight, contentHeight) && contentHeight > wrapHeight) {
    wrapStyle.overflowY = 'scroll'
  }

  return <div ref={wrapRef} className='main'>
    <div className='wrap' style={wrapStyle}>
      <div ref={contentRef} className='content' style={{width:contentWidth}}>
        <div className="head">
          <HeadCell text1='　' text2='名称' type={'name'} style={idS} setStyle={setIdS} onClick={()=>props.switchSort('name')}></HeadCell>
          <HeadCell text1={`${props.gcpu}%`} text2='CPU' type={'cpu'} style={cpuS} setStyle={setCpuS} onClick={()=>props.switchSort('cpu')}></HeadCell>
          <HeadCell text1={`${props.gmem}%`} text2='内存' type={'mem'} style={memS} setStyle={setMemS} onClick={()=>props.switchSort('mem')}></HeadCell>
        </div>
        <div>
          <Columns idS={idS} cpuS={cpuS} memS={memS} tag='应用' items={props.rows}></Columns>
        </div>
      </div>
    </div>
  </div>
  
}

//<Scrollable eRef={eRef} className="h-100 w-100 over-hidden flex-1"></Scrollable>
//<Columns idS={idS} cpuS={cpuS} memS={memS} tag='系统应用' items={props.system}></Columns>

const Columns = props => {
  const {items,tag,...other} = props;

  return <div>
    <div className='tag'>
      <div className='cell text' style={props.idS}><span>{tag}</span></div>
      <div className="cell" ui-type='other' style={props.cpuS}></div>
      <div className="cell" ui-type='other' style={props.memS}></div>
    </div>
    {items.map(item => {
      return <Column {...other} key={item.pid} {...item}></Column>
    })}
  </div>
}

const Column = props => {
  const {pid} = props
  const onClick = e=>{
    e.taskmgrSelectedPid = props.pid;
  }

  const selectPid = useContext(SelectPidContext);
  const selected = selectPid === pid

  return <div className='column' ui-selected={`${selected}`} onClick={onClick}>
    <div style={props.idS} className='cell' ui-type='name'>
      <div className='icon'>{props.icon}</div>
      <span className='name'>{props.name}</span>
    </div>
    <div className='cell' ui-type='other' style={props.cpuS}><span>{props.cpu}%</span></div>
    <div className='cell' ui-type='other' style={props.memS}><span>{props.mem} MB</span></div>
  </div>
}

const HeadCell = props => {
  const {type, text1, text2, style, setStyle, ...other} = props;
  const uItype = type === 'name' ? 'name' : 'other'

  const sort = useContext(SortContext);

  const selected = sort.type === type
  const asc = sort.asc

  const elRef=useRef();
  
  return <div ref={elRef} className={`cell`} 
  ui-selected={g.toString(selected)}
  ui-asc={g.toString(asc)}
  ui-type={uItype}
  style={style} {...other}>
    <span className="text-1">{text1}</span>
    <span className="text-2">{text2}</span>
    <Resizeable setStyle={setStyle} targetRef={elRef} minWidth={80} size={8} resizeable={{e:true}}/>
  </div>
}

export const renderTaskbar = () => {
  return <Taskmgr/>
}