import { useEffect, useRef } from 'react'
import { getEffect, GlobalEventHandle } from './GlobalEventHandle';


export default function Draggable(props) {
  const {threshold, onPointerDown, onDragStart, onDrag, onDragEnd, ...other} = props;

  const downing = useRef(false);
  const draging = useRef(false);
  const beginX = useRef();
  const beginY = useRef();

  const dragHandle = useRef();
  dragHandle.current = onDrag;
  const dragEndHandle = useRef();
  dragEndHandle.current = onDragEnd;
  const dragStartHandle = useRef();
  dragStartHandle.current = onDragStart;

  const dragThreshold = useRef();
  dragThreshold.current = threshold !== undefined ? threshold : 0;

  useEffect(() => {
 
    function move(e) {
      if(downing.current) {
        const x = e.clientX;
        const y = e.clientY;
        const dx = x - beginX.current;
        const dy = y - beginY.current;
        if(!draging.current && (dx**2 + dy**2) >= dragThreshold.current**2) {
          draging.current = true;
          dragStartHandle.current({dx,dy,bx:beginX.current,by:beginY.current,ex:x,ey:y});
        }
        if(draging.current) {
          dragHandle.current({dx,dy,bx:beginX.current,by:beginY.current,ex:x,ey:y});
        }
      }
    }
  
    function end(e) {
      if(draging.current) {
        const x = e.clientX;
        const y = e.clientY;
        const dx = x - beginX.current;
        const dy = y - beginY.current;
        if(dragEndHandle.current) {
          dragEndHandle.current({dx,dy,bx:beginX.current,by:beginY.current,ex:x,ey:y});
        }
        draging.current = false;
      }
      downing.current = false;
    }

    return getEffect('pointermove',move,'pointerup', end, 'pointerleave',end);
  },[])

  function handlePDown(e) {
    downing.current = true;
    beginX.current = e.clientX;
    beginY.current = e.clientY;
    onPointerDown && onPointerDown(e);
  }

  return <div {...other}
  onPointerDown={handlePDown}
  onDragStart={e => e.preventDefault()}
  >
    {props.children}
  </div>
}

