import { useContext, useEffect, useRef, useState } from 'react'
import { ImmobilizationContext } from '../../Windows11'
import './SnakeGame.sass'

export const SnakeGame = props => {
  const {pid} = props
  const title = useRef(`snake-game-${pid}`)

  const {windowsSub} = useContext(ImmobilizationContext)
  const thisWindow = windowsSub.get()[pid]
  const [focus, setFocus] = useState(thisWindow.focus)

  useEffect(() => {
    const linstener = val => {
      const thisWindow = val[pid]
      if(thisWindow) {

        if(thisWindow.focus !== focus) {
          setFocus(thisWindow.focus)
        }
      }
    }

    windowsSub.subscribe(linstener)

    return () => windowsSub.unsubscribe(linstener)
  },[focus, pid, windowsSub])

  return <div className='snake-game' ui-focus={`${focus}`}>
    <iframe className='main' title={title} src='https://hinako0102.github.io/snake.github.io/'></iframe>
    <div className='cover'/>
  </div>
}

export const renderSnakeGame = ({pid,windowStyle,setWindowStyle}) => {
  return <SnakeGame pid={pid}/>
}