class Node{
  constructor(value) {
    this.value = value;
    this.next = undefined;
  }
}

export default class List{
  constructor() {
    this.frist = undefined;
    this.last = undefined;
    this.size = 0;
  }

  *[Symbol.iterator]() {
    let node = this.frist;
    while(node !== undefined) {
      const value = node.value;
      node = node.next;
      yield value;
    }
  }

  insert(value) {
      if(this.size === 0) {
        this.frist = new Node(value);
        this.last = this.frist;
      }
      else {
        this.last.next = new Node(value);
        this.last = this.last.next;
      }
      ++this.size;
  }
}

export class SkipList{
  constructor() {
    this.arr = [];
    this.size = 0;
    this.voidIndex = 0;
  }

  *[Symbol.iterator]() {
    for(let v of this.arr) {
      yield v;
    }
  }

  insert(value, index) {
    if(index) {
      if(this.arr[index]) {
        this.arr = this.arr.slice(0,index).concat([value]).concat(this.arr.slice(index));
      } else {
        this.arr[index] = value;
      }
      while(this.arr[this.voidIndex] !== undefined) {
        this.voidIndex++;
      }
    } else {
      this.arr[this.voidIndex] = value;
      this.voidIndex++;
      while(this.arr[this.voidIndex] !== undefined) {
        this.voidIndex++;
      }
    }
    ++this.size;
  }

  getFristNextVoid(node) {
    while(node.next !== undefined && node.next.value !== undefined) {
      node = node.next;
    }
    return node;
  }

  toString() {
    let str = '';
    for(let v of this.arr) {
      str += '{'+v.toString+'}';
    }
    return str;
  }
}