export const g = {
  e:(selector) => {
    return document.querySelector(selector);
  },
  eid:(id) => {
    return document.getElementById(id);
  },
  notUndefined : (...args) => {
    for(let arg of args) {
      if(arg === undefined) {
        return false;
      }
    }
    return true;
  },
  toPx: (number) => {
    return `${number}px`
  },
  toString: (value) => {
    return `${value}`
  },
  deepEquals: (o1, o2) => {
    if(o1 === o2) {
      return true
    }
    if(typeof o1 !== 'object' || typeof o1 !== o2) {
      return false
    }
    for(let key of Object.keys(o1)) {
      if(!g.deepEquals(o1[key], o2[key])) {
        return false
      }
    }
    return true
  }
}

