import { useContext, useEffect, useRef } from 'react'
import { ImmobilizationContext } from '../Windows11';

export default function DownAndGlobalUp(props) {

  const {windows11GlobalEventHandle} = useContext(ImmobilizationContext)

  const {children, onGlobalUp} = props;

  useEffect(() => {
    windows11GlobalEventHandle.pointerup.add(onGlobalUp)
    return () => windows11GlobalEventHandle.pointerup.delete(onGlobalUp)
  },[onGlobalUp,windows11GlobalEventHandle])

  return (
    <>
      {children}
    </>
  )
}