const DEFAULT_LENGTH = 8;

export default class Vector{
  constructor(tSize = DEFAULT_LENGTH, init = true) {
    if(init) {
      if(tSize <= 0) {
        tSize = DEFAULT_LENGTH;
      }
      this.tSize = Math.ceil(tSize / 8) * 8;
      this.arr = new Array(this.tSize);
      this._size = 0;
    }
  }

  get size() {
    return this._size;
  }
  
  push(value)
  {
    if(this.tSize <= this._size) {
      this.grow();
    }
    this.arr[this._size] = value;
    ++this._size;
  }

  grow()
  {
    this.tSize *= 2;
    const old = this.arr;
    this.arr = new Array(this.tSize);
    for(let l = 0; l < this._size;++l) {
      this.arr[l] = old[l];
    }
  }

  pop()
  {
    this.arr[this._size-1] = undefined;
    --this._size;
  }

  insert(index, value)
  {
    this.checkIndex(index);

    if(this.tSize <= this._size) {
      this.grow();
    }

    for(let l = this._size - 1; l >= index;--l) {
      this.arr[l+1]=this.arr[l];
    }

    this.arr[index] = value;
    ++this._size;
  }

  checkIndex(index) {
    if(index < 0)
    {
      throw new Error(`索引必须为大于等于0的整数 index=${index}`);
    }

    if(index > this._size) {
      throw new Error(`索引越界 size=${this._size} index=${index}`);
    }
  }

  delete(index) {
    this.checkIndex(index);

    for(let l = index; l < this._size - 1;++l) {
      this.arr[l] = this.arr[l+1];
    }

    --this._size;
  }

  deleteBy(condition, simple = true) {
    let done = false;
    for(let l = 0; l < this._size;) {
      if(condition(this.arr[l])) {
        done = true;
        this.delete(l);
        if(simple) {
          break;
        }
      } else {
        ++l;
      }
    }
    return done;
  }

  deleteByValue(value, simple = true) {
    return this.deleteBy((v) => {return v === value}, simple);
  }

  *[Symbol.iterator] () {
    for(let l = 0; l < this._size; ++l) {
      yield this.arr[l];
    }
  }

  clone() {
    const clone = new Vector(0,false);
    clone.arr = this.arr;
    clone._size = this._size;
    clone.tSize = this.tSize;
    return clone();
  }
}