import {React, useEffect, useRef } from "react";
import { getEffect } from './GlobalEventHandle';
import './Resizeable.css'

export const Resizeable = props => {

  const {minWidth,minHeight,size,targetRef,setStyle, onResizeStart, onResizeEnd} = {...props};

  const bx = useRef(), by= useRef(),bw = useRef(), bh= useRef(),bl = useRef(), bt= useRef();
  const dataType = useRef();
  const resizing = useRef(false);

  const start = (e) => {
    bx.current = e.clientX;
    by.current = e.clientY;
    dataType.current = e.currentTarget.getAttribute('data-type');
    resizing.current = true;

    const rect = targetRef.current.getBoundingClientRect();
    bw.current = rect.width;
    bh.current = rect.height;
    bl.current = rect.left;
    bt.current = rect.top;

    if(onResizeStart)
    {
      onResizeStart(e)
    }
  }

  useEffect(() => {
    //console.log('resize effect')
    

    const move = (evt) => {
      //console.log('resize move');
      if(resizing.current) {
       // console.log('resize onresize move');

        const ex = evt.clientX;
        const ey = evt.clientY;
    
        const e = (style) => {
          if(ex - bx.current + bw.current >= minWidth) {
            style.width = ex - bx.current + bw.current;
          } else {
            style.width = minWidth;
          }
        }
        const s = (style) => {
          if(ey - by.current + bh.current >= minHeight) {
            style.height = ey - by.current + bh.current;
          } else {
            style.height =minHeight;
          }
        }
        const w = (style) => {
          if(bx.current-ex <=bl.current) {
            if(bx.current - ex + bw.current > minWidth) {
              style.width = bx.current - ex + bw.current;
              style.left = ex - bx.current + bl.current;
            } else {
              style.width = minWidth;
              style.left = bl.current + bw.current - minWidth;
            }
          }
          else
          {
            style.left = 0;
            style.width = bl.current + bw.current;
          }
        }
        const n = (style) => {
          if(by.current-ey<=bt.current) {
            if(by.current - ey + bh.current > minHeight) {
              style.height = by.current - ey + bh.current;
              style.top = ey - by.current + bt.current;
            } else {
              style.height = minHeight;
              style.top = bt.current + bh.current - minHeight;
            }
          } else {
            style.top = 0;
            style.height = bt.current + bh.current;
          }
          //console.log('top='+style.top+'ey='+ey+'');
        }
        const func = {
          e,
          s,
          w,
          n,
          se:(style)=>{e(style);s(style)},
          sw:(style)=>{w(style);s(style)},
          ne:(style)=>{e(style);n(style)},
          nw:(style)=>{w(style);n(style)},
        }
  
        setStyle(style => {
          const newStyle = {...style};
          func[dataType.current](newStyle);
          return newStyle;
        });
      }
    }

    const end = (e) => {
      if(resizing.current) {
        resizing.current = false;
        if(onResizeEnd){
          onResizeEnd(e)
        }
      }
    }

    return getEffect('pointermove', move,'pointerup', end,'pointerleave', end);
  },[minHeight,minWidth,setStyle,onResizeEnd])

  return (
    <>
      {props.resizeable.n && <div data-type='n'   style={{height:size}} onPointerDown={start} className="resizeable left top z-10"></div>}
      {props.resizeable.e && <div data-type='e'   style={{width:size}} onPointerDown={start} className="resizeable right top z-10"></div>}
      {props.resizeable.s && <div data-type='s'   style={{height:size}} onPointerDown={start} className="resizeable left bottom z-10"></div>}
      {props.resizeable.w && <div data-type='w'   style={{width:size}} onPointerDown={start} className="resizeable left top z-10"></div>}
      {props.resizeable.nw && <div data-type='nw' style={{width:size,height:size}} onPointerDown={start} className="resizeable left top z-20"></div>}
      {props.resizeable.ne && <div data-type='ne' style={{width:size,height:size}} onPointerDown={start} className="resizeable right top z-20"></div>}
      {props.resizeable.sw && <div data-type='sw' style={{width:size,height:size}} onPointerDown={start} className="resizeable left bottom z-20"></div>}
      {props.resizeable.se && <div data-type='se' style={{width:size,height:size}} onPointerDown={start} className="resizeable right bottom z-20"></div>}
    </>
  )
}
