import { useContext, useEffect, useRef, useState } from "react";
import { getEffect } from '../utils/GlobalEventHandle';
import List, { SkipList } from '../utils/List';
import Sort from '../utils/Sort';
import { config, ImmobilizationContext } from '../Windows11';
import './Desktop.css'

export const Desktop = props => {

  const ctrl = useRef();

  const mod = useRef(modInit(config.desktop));

  const [items,setItems] = useState([...mod.current.items]);

  const update = useRef(() => setItems([...mod.current.items]));

  const unSelectAll = useRef(()=> {
    for(let item of mod.current.items) {
      item.selected = false;
    }
  });  

  const desktopRef = useRef();

  useEffect(() => {
    
    const onResize = () => {
      const {x,y} = getDesktopXY();
      mod.current.resize(x,y);
      update.current();
    }

    return getEffect('resize',onResize);
  },[])

  /**
   * Select
   */

  const selecting = useRef(false);
  const [selectValue, setSelectValue] = useState();
  const sbx = useRef();
  const sby = useRef();
  const sex = useRef();
  const sey = useRef();

  const selectBegin = (e) => {
    selecting.current = true;
    const {x,y} = getRangeXY(e);
    bx.current = e.clientX;
    by.current = e.clientY;

    sbx.current = sex.current = x;
    sby.current = sey.current = y;

    setSelectValue({
      cssex: e.clientX,
      cssey: e.clientY
    })
  }

  const inSelectRange = (x,y) => {

    if(selecting.current) {
      let sx,sy,bx,by;

      if(sex.current > sbx.current) {
        sx = sbx.current;
        bx = sex.current;
      } else {
        sx = sex.current;
        bx = sbx.current;
      }
      if(sey.current > sby.current) {
        sy = sby.current;
        by = sey.current;
      } else {
        sy = sey.current;
        by = sby.current;
      }

      const checkx = x * 2;
      const checky = y * 2;

      if(sx <= checkx && checkx <= bx && sy <= checky && checky <= by) {
        return true;
      } else {
        return false;
      }
    } else {
      return false;
    }
  };

  useEffect(() => {
    const move = e=>{
      if(selecting.current) {
        const {x, y} = getRangeXY(e);
        sex.current = x;
        sey.current = y;
        setSelectValue({
          cssex: e.clientX,
          cssey: e.clientY
        })
      }
    }

    const end = e=>{
      if(selecting.current) {
        const {x, y} = getRangeXY(e);
        sex.current = x;
        sey.current = y;

        for(let item of mod.current.items) {
          item.selected = item.selected ^ inSelectRange(item.x,item.y);
        }
        selecting.current = false;

        update.current();
      }
    }

    return getEffect('pointermove',move, 'pointerup', end,'pointerleave', end);
  },[])

  /**
   * click
   */

  const doItemPointerEvent = useRef((v) => {
    selectedV.current = -1
    if(ctrl.current) {
      mod.current.items[v].selected = !mod.current.items[v].selected;

      update.current();
    } else {

      unSelectAll.current();
      mod.current.items[v].selected = true;
      update.current();
    }
  });

  /**
   * Drag
   */

  const draging = useRef(false);
  const bx = useRef();
  const by = useRef();
  const dDowning = useRef(false);
  const [dragValue, setDragValue] = useState();

  const dragBegin = (e) => {
    dDowning.current = true;
    bx.current = e.clientX;
    by.current = e.clientY;
  }


  useEffect(() => {

    const move = (e) => {
      if(dDowning.current) {
        const x = e.clientX;
        const y = e.clientY;
        if(!draging.current) {
         // console.log(((x-bx.current)**2 + (y-by.current)**2));
          draging.current = (((x-bx.current)**2 + (y-by.current)**2) >= 64);
        }
        
        if(draging.current) {
          setDragValue({
            x:e.clientX,
            y:e.clientY
          })
        }
      }
    }

    const leave = e => {
      if(draging.current) {
        selectedV.current = -1;
        
        draging.current = false;

        let dx = Math.round((e.clientX - bx.current) / 76);
        let dy = Math.round((e.clientY - by.current) / 100);

        mod.current.shift(dx,dy)

        update.current();
      }
      dDowning.current = false;
    }

    const up = e => {
      if(draging.current) {
        leave(e);
      } else {
        if(selectedV.current !== -1) {
          doItemPointerEvent.current(selectedV.current);
        }
      }
      dDowning.current = false;
    }

    return getEffect('pointermove',move, 'pointerup', up,'pointerleave', leave);
  },[])


  useEffect(() => {
    const handleKeyDown = e=>{
      if(e.ctrlKey) {
        ctrl.current = true;
      }
    }
    const handleKeyUp = e=>{
      if(!e.ctrlKey) {
        ctrl.current = false;
      }
    }

    return getEffect('keydown', handleKeyDown, 'keyup',handleKeyUp);
  },[])

  const desktopPointerDown = (e) => {
    e.programId = 'desktop';

    if(!e.onItem) {
      if(!ctrl.current) {
        unSelectAll.current();
        update.current();
      }
      selectBegin(e);
    } else {
      dragBegin(e);
    }
  }

  const selectedV = useRef(-1);

  const itemPointerDown = (v) => {
    return (e) => {
      e.onItem = true;
      e.itemV = v;

      const selected = mod.current.items[v].selected;

      if(selected) {
        selectedV.current = v;
      } else {
        doItemPointerEvent.current(v);
      }
    }
  }

  const getRangeSelectCssStyle = () => {
    if(selecting.current) {
      const left = bx.current < selectValue.cssex ? bx.current : selectValue.cssex;
      const top = by.current < selectValue.cssey ? by.current : selectValue.cssey;
      const width = Math.abs(bx.current-selectValue.cssex);
      const height = Math.abs(by.current-selectValue.cssey);
      return {
        left,
        top,
        width,
        height
      }
    }
    else{
      return {}
    }
  }


  const getDragStyle = () => {
    if(draging.current) {
      return {
        transform:`translate(${dragValue.x - bx.current}px,${dragValue.y - by.current}px)`
      }
    }
    else{
      return {}
    }
  }

  return (
    <div className='desktop wh-100 over-hidden relative font12 select-none'
    onPointerDown={desktopPointerDown}
    ref={desktopRef}
    >
      {items && items.map((item, index) => {
        const {selected,...otherPoprs} = item;
        return <Item onPointerDown={itemPointerDown(item.v)} key={index} {...otherPoprs} selected={selected ^ inSelectRange(otherPoprs.x, otherPoprs.y)}></Item>
      })}
      <div className={"absolute select-range-css" + (selecting.current ? '' :' display-none' )}
      style={getRangeSelectCssStyle()}
      ></div>
      <div className={`absolute w-100 h-100 top left desktop-drag-css${draging.current? '':' display-none'}`} style={getDragStyle()}>
        {draging.current && items && items.map((item, index) => {
          return item.selected ? <DragItem key={index} {...item}></DragItem> : null;
        })}
      </div>
    </div>
  )
}

const Item = props => {
  const actions = 0;//useContext(Actions);

  const className = 'desktop-item absolute none-select' + (props.selected ? ' selected' : '');

  const nameClass = 'block font-12px white desktop-item-name' + (props.selected ? '' : ' nowrap-over-hidden-elli');

  const style = {
    top: props.y * 100 + 5,
    left: props.x * 76
  }

  if(props.selected) {
    style.zIndex = 1;
  }

  const aid = props.aid
  const {run} = useContext(ImmobilizationContext)
  const onDoubleClick = () => {
    run(aid)
  }

  return (
    <div className={className}
    onDoubleClick={onDoubleClick}
    onPointerDown={props.onPointerDown}
    onDragStart={e => e.preventDefault()}
    style={style}
    >
      <div className='css' aria-hidden='true'>
        <div className='desktop-item-main border-box w-100 h-100'>
          <div className='h-48 w-48 desktop-item-icon'>
            {props.getIcon()}
          </div>
          <span className={nameClass}>{props.name}</span>
        </div>
      </div>
    </div>
  )
}
/*<img className='h-48 w-48 desktop-item-icon' src={props.icon} alt='icon' ></img>*/

const DragItem = props => {
  const style = {
    top: props.y * 100 + 5,
    left: props.x * 76
  }

  return (
    <div onDragStart={e => e.preventDefault()}
    className='desktop-drag-item absolute none-select'
    style={style}
    >
      <div className='css' aria-hidden='true'>
        <div className='desktop-item-main border-box w-100 h-100'>
        <div className='h-48 w-48 desktop-item-icon'>
            {props.getIcon()}
          </div>
        </div>
      </div>
    </div>
  )
}

const modInit = (()=>{
  const map = new Map();
  return (items) => {
    if(!map.has(items)) {
      const {x,y} = getDesktopXY();
      const mod = new DesktopMod(true, x,y,items);
      map.set(items, mod);
    }
    return map.get(items);
  }
})();

const getDesktopXY = () => {
  return {y:(Math.floor((document.body.clientHeight - 40 - 5 + 28) / 100)),x:Math.floor(document.body.clientWidth / 76)}
}

const getRangeXY = (e) => {
  const x = Math.floor((e.clientX - 19) / 38);
  let y;
  const tmp = e.clientY - 5 - 18;
  if(tmp < 0) {
    y = -1;
  } else {
    const rem = tmp % 100;
    y = Math.floor(tmp / 100) * 2;
    if(rem >36) {
      y++;
    }
  }
  return {x,y};
}

class DesktopMod{
  constructor(init = false,x,y,items) {

    if(init) {
      this.items = items;
      this.x = x;
      this.y = y;
      this.grid = [];

      for(let l = 0,i = 0;l < x || i < items.length;++l) {
        this.grid[l] = [];
        for(let m = 0;m < y;++m,++i) {
          if(i < items.length) {
            this.grid[l][m] = i;
            this.items[i].x = l;
            this.items[i].y = m;
            this.items[i].v = i;
            this.items[i].selected = false;
          }
          else {
            this.grid[l][m] = -1;
          }
        }
      }
    }
  }

  get(x,y) {
    if(x < 0 || y < 0 || y >= this.y) {
      return -2;
    }
    this.checkAndFill(x,y);
    try{
      return this.grid[x][y];
    } catch {
      throw new Error('this.grid[x][y] err');
    }
    //return this.grid[x][y];
  }

  set(x,y,v) {
    this.checkAndFill(x,y);
    this.grid[x][y] = v;
  }

  checkAndFill(x,y) {
    if(x >= this.grid.length) {
      for(let l = this.grid.length;l <= x;++l) {
        this.grid[l] = new Array(this.grid[0].length);
        this.grid[l].fill(-1);
      }
    }
    if(y >= this.grid[0].length) {
      for(let l = 0; l < this.grid.length;++l) {
        for(let m = this.grid[0].length;m <=y;++m) {
          this.grid[l][m] = -1;
        }
      }
    }
  }

  isOver(x,y) {
    return x<0 || y<0 || x>=this.x || y>=this.y;
  }

  shift(dx,dy) {
    const notOver = new Sort((i1,i2) => {return i1.x < i2.x || (i1.x === i2.x && i1.y < i2.y)});
    const over = new Sort((i1,i2) => {return i1.v < i2.v});
    for(let item of this.items) {
      if(item.selected) {

        this.set(item.x,item.y,-1);

        item.x += dx;
        item.y += dy;

        if(this.isOver(item.x,item.y)) {
          over.insert(item);
        } else {
          notOver.insert(item);
        }
      }
    }

    if(notOver.size > 0) {
      //console.log('notOver');
      //console.log(notOver);
      this.insert([...notOver]);
    }
    if(over.size > 0) {
      this.overflowRelayout(over);
    }
  }

  resize(x,y) {
    this.x = x;
    this.y = y;
    const over = new Sort((i1,i2) => {return i1.v < i2.v});
    for(let item of this.items) {
      if(this.isOver(item.x,item.y)) {
        this.set(item.x,item.y,-1);
        over.insert(item);
      }
    }

    this.overflowRelayout(over);
   // console.log(this);
  }

  getVoidSizeBeginWith(x,y) {
    let i = 0;
    for(let l = x; l < this.x; ++l) {
      for(let m = 0; m < this.y;++m) {
        if((l > x || m >= y) && this.get(l,m) === -1) {
          ++i;
        }
      }
    }
    return i;
  }

  insert(items) {
    const getIter = (x,y) => {
      const i = {
        next:()=>{
          if(y < this.y - 1) {
            ++y;
          } else {
            y = 0;
            ++x;
          }
          return i;
        },
        prev:()=>{
          if(y > 0) {
            --y;
          } else {
            y = this.y-1;
            --x;
          }
          if(x < 0) {
            throw new Error('x不得小于0');
          }
          return i;
        },
        get:()=>{
          return {x,y,v:this.get(x,y)};
        }
      }
      return i;
    }

    const arrange = (items,iter,vs,cs) => {
      try{
        let voidSub = 0;
        const {x,y,v} = iter.get();
        //console.log('arrange x='+x+'y='+x+'v='+v);
        cs.push({x,y});
        if(items.length > 0 && items[0].x === x && items[0].y === y) {
          //console.log('item match')
          //console.log(items[0]);
          //console.log(cs);
          vs.insert(items[0].v, cs.length - 1);
          items.shift();
        }

        if(v !== -1) {
         // console.log('insert v='+v);
          vs.insert(v);
        } else {
          ++voidSub;
        }
        return voidSub;
      } catch(e) {
        throw e;
      }
    }

    const doInsert = (vs,cs) => {
      /*
      console.log('do insert');
      console.log('vs');
      console.log(vs);
      console.log('cs');
      let consolecs='';
      for(let c of cs) {
        consolecs+=`{x=${c.x} y=${c.y}}`;
      }
      console.log(consolecs);
      */

      for(let v of vs) {
        /*
        console.log('v');
        console.log(v);
        console.log('c');
        console.log(cs[0]);
        */
        const c = cs[0];
        this.items[v].x = c.x;
        this.items[v].y = c.y;
        this.set(c.x,c.y,v);
        cs.shift(); 
      }
    }

    let afterVoidSize = this.getVoidSizeBeginWith(items[0].x,items[0].y);
    //console.log('尝试正序');
    //console.log('afterVoidSize='+afterVoidSize);
    while(items.length > 0 && afterVoidSize > items.length) {
      //console.log(afterVoidSize);
      const iter = getIter(items[0].x,items[0].y);
      const vs = new List();
      vs.insert(items[0].v)
      items.shift();
      const cs = [];
      
      while(vs.size > cs.length) {
        //console.log('afterVoidSize='+afterVoidSize);
        const voidSub = arrange(items,iter,vs,cs);
        iter.next();
        afterVoidSize-=voidSub;
      }
      //console.log('afterVoidSize='+afterVoidSize);
      //console.log('正序');
      doInsert(vs,cs);

      if(items.length > 0) {
        afterVoidSize = this.getVoidSizeBeginWith(items[0].x,items[0].y);
      }
    }

    if(items.length > 0) {
      /*
      for(let i of items) {
        console.log(i);
      }
    */
      items.reverse();
      const iter = getIter(this.x - 1,this.y - 1);
      const vs = new SkipList();
      const cs = [];

      //console.log('reverse vs');
      //console.log(vs);
      //console.log('尝试反序');
      //console.log(items);
      /*
      for(let i of items) {
        console.log(i);
      }
      */
      while(vs.size > cs.length || items.length > 0) {
        arrange(items,iter,vs,cs);
        //console.log(items);
        iter.prev();
      }
      //console.log('反序');
      doInsert(vs,cs);
    }
  }

  overflowRelayout(items) {
    //console.log('over items');
    //console.log(items);
    const buffer = {};

    /*

    for(let l = this.x;l < this.grid.length; ++l) {
      for(let m = 0;m < this.grid[0].length;++m) {
        this.set(l,m,-1);
      }
    }
    */


    const relayoutDo = (overItem) => {

      while(this.get(overItem.target.x,overItem.target.y) !== -1) {
        //console.log(`get x=${overItem.target.x} y=${overItem.target.y} v=${this.get(overItem.target.x,overItem.target.y)}`)
        overItem.next();
      }
      //console.log(`get x=${overItem.target.x} y=${overItem.target.y} v=${this.get(overItem.target.x,overItem.target.y)}`)

      this.grid[overItem.target.x][overItem.target.y] = overItem.item.v;
      overItem.item.x = overItem.target.x;
      overItem.item.y = overItem.target.y;
    }

    for(let item of items) {
      const over = new OverItem(item,this.x,this.y,buffer);
      //console.log(over.origin);
      relayoutDo(over);
      
      //console.log('relay x=' + over.target.x + ' y=' + over.target.y);
      //console.log();
      over.next();
      buffer[over.getKey()] = over.target;
    }
  }

  clone() {
    //console.log('clone');
    const result = new DesktopMod(false);
    result.items = this.items;
    result.x = this.x;
    result.y = this.y;
    result.grid = this.grid;
    return result;
  }
}

const shiftyjumpx = (c,oy,ey,increment)=>{
  return shifty(c, oy, ey - 1, true)
  || jumpx(c,ey,oy,increment)
  || shifty(c, 1, oy-1, false)
  || jumpx(c, 0, oy - 1, increment);
}

const shiftxjumpy = (c,ox,ex, increment)=>{
  //console.log('shiftxjumpy');
  return shiftx(c, ox, ex-1, true)
  || jumpy(c, ex, ox, increment)
  || shiftx(c,1, ox-1,false)
  || jumpy(c, 0, ox-1, increment)
}


const shifty = (c,min,max,increment = true) => {
  if(c.y >= min && c.y <= max) {
    if(increment) {
      ++c.y;
    } else {
      --c.y;
    }
    return true;
  } else {
    return false;
  }
}

const shiftx = (c,min,max,increment = true) => {
  /*
  console.log('shiftx cx'+c.x);
  console.log('shiftx min ' + min);
  console.log('shiftx max ' + max);
  console.log('shiftx increment ' + increment);
  */

  if(c.x >= min && c.x <= max) {
    //console.log(`do shiftx x=${c.x} mix=${min} max=${max}`);
    if(increment) {
      //console.log('++x');
      ++c.x;
    } else {
      //console.log('--x');
      --c.x;
    }
    return true;
  } else {
    return false;
  }
}

const jumpx = (c,matchy,goaly,increment = true) => {
  if(c.y === matchy) {
    if(increment) {
      ++c.x;
    } else {
      --c.x;
    }
    c.y = goaly;
    return true;
  } else {
    return false;
  }
}

const jumpy = (c,matchx,goalx,increment = true) => {
  //console.log('macthx=' + goalx);
  //console.log('goalx=' + goalx);
  if(c.x === matchx) {
    //console.log('do jumpy');
    if(increment) {
      ++c.y;
    } else {
      --c.y;
    }
    c.x = goalx;
    return true;
  } else {
    return false;
  }
}

const jump = (c,matchx,matchy,goalx,goaly, ex,endy) => {
  if(c.x === matchx && c.y===matchy) {
    //console.log('do jump');
    if(goalx < 0 || goalx > ex ||  goaly < 0 || goaly > endy) {
      c.x = ex + 1;
      c.y = 0;
    } else {
      c.x = goalx;
      c.y = goaly;
    }
    return true;
  } else {
    return false;
  }
}

const over = (c,x,y) => {
  if(c.x >= x) {
    if(c.y < y) {
      ++c.y;
    } else {
      c.y = 0;
      ++c.x;
    }
    return true;
  } else {
    return false;
  }
}

class OverItem{
  constructor(item,x,y,buffer) {
    if(item.x > 0 && item.x < x && item.y>0&& item.y < y) {
      throw new Error('项 '+ item.v + ' 非溢出项');
    }

    this.end = {
      x:x-1,
      y:y-1
    }

    //console.log('ex='+this.end.x)

    this.item = item;
    if(item.x < 0) {
      this.type = 'left';
      this.origin = {x:0,
        y: item.y <= this.end.y
        ? y >= 0 
          ? item.y 
          : 0
        : this.end.y};
    } else if(item.x >= x) {
      this.type = 'right';
      this.origin = {x:this.end.x,
        y: item.y <= this.end.y
        ? y >= 0 
          ? item.y 
          : 0
        : this.end.y};
    } else if(item.y < 0) {
      this.type = 'up';
      this.origin = {x:item.x,y: 0};
    } else {
      this.type = 'down';
      this.origin = {x:item.x,y: this.end.y};
    }
    
    this.target = buffer !== undefined && buffer[this.getKey()] !== undefined ? {...buffer[this.getKey()]} : {...this.origin};
    //console.log('getKey='+this.getKey());
    //console.log('target='+'x'+this.target.x+'y'+this.target.y);
    //console.log('ex'+this.x);
  }

  getKey() {
    return 'x'+this.origin.x+'y'+this.origin.y;
  }

  next() {
    this.target = funcs[this.type](this.target,this.origin.x,this.origin.y,this.end.x,this.end.y);
    //console.log('next='+'x'+this.target.x+'y'+this.target.y)
  }
}

const funcs = {
  'right':(c,ox,oy,ex,ey) => {
    over(c,ex+1,ey)
    || jump(c, 0, ey, ox, oy-1, ex,ey)
    || jump(c, -1,oy-1, ex+1,0,ex,ey)
    || shiftyjumpx(c,oy,ey, false);
    return c;
  },
  'left':(c,ox,oy,ex,ey) => {
    over(c,ex+1,ey)
    || jump(c, ex, ey, ox, oy-1,ex,ey)
    || jump(c, ex+1, oy+1, ex+1,0,ex,ey)
    || shiftyjumpx(c,oy,ey, true);
    return c;
  },
  'up':(c,ox,oy,ex,ey) => {
    over(c,ex+1,ey)
    || jump(c,ex,ey,ox-1,oy, ex,ey)
    || jump(c,ox-1,ey+1, ex+1,0,ex,ey)
    || shiftxjumpy(c,ox,ex,true);
    return c;
  },
  'down':(c,ox,oy,ex,ey) => {
    
    over(c,ex+1,ey)
    || jump(c,ex,0,ox-1,oy,ex,ey)
    || jump(c,ox-1,-1,ex+1,0,ex,ey)
    || shiftxjumpy(c,ox,ex,false);
    //console.log('cx' + c.x);
    return c;
  }
}
