export default class Sort{
  /**
   * 
   * @param {function(v1,v2)} less return v1 < v2
   */
  constructor(less) {
    this.less = less;
    this.head = undefined;
    this.size = 0;
  }

  *[Symbol.iterator]() {
    let node = this.head;
    while(node !== undefined) {
      const value = node.value;
      node = node.next;
      yield value;
    }
  }

  insert(value) {
    if(this.size === 0) {
      this.head = new Node(value);
    }
    else {
      let prev = undefined;
      let node = this.head;
      while(node!==undefined && !this.less(value, node.value)) {
        prev = node;
        node = node.next;
      }

      let newNode = new Node(value);

      if(prev === undefined) {
        newNode.next = this.head;
        this.head = newNode;
      } else {
        const oNext = prev.next;
        newNode.next = oNext;
        prev.next = newNode;
      }

    }
    ++this.size;
  }
}

class Node{
  constructor(value) {
    this.value = value;
    this.next = undefined;
    this.prev = undefined;
  }
}



