import { createContext, useContext, useEffect, useRef, useState } from 'react';
import { ArrowDown, ArrowUp } from '../Base/Arrow';
import { g } from '../utils/Function';
import { getEffect } from '../utils/GlobalEventHandle';
import { useUpdate } from '../utils/Hooks';
import { ImmobilizationContext } from '../Windows11';
import './Calendar.sass'

const CalendarContext = createContext();

class Time{
  yearRange
  year
  month
  date

  constructor(year,month,date) {
    this.year = year
    this.month = month
    this.date = date
    if(g.notUndefined(year)){
      this.yearRange = YearRange.getYearRangeOfYear(year)
    }
  }

  equalsDate(other) {
    return this.date===other.date && this.month === other.month && this.year === other.year;
  }

  equalsMonth(other) {
    return this.month === other.month && this.year === other.year;
  }

  equalsYear(other) {
    return this.year === other.year;
  }

  inYearRange(yearRange) {
    return yearRange.begin <= this.year && yearRange.end >= this.year;
  }

  getDatesIter(front = true) {
    const daySizeMapping = [undefined,31,28,31,30,31,30,31,31,30,31,30,31];
    const max = this.month===2&&(this.year % 4)===0 ? 29 : daySizeMapping[this.month]
    const iter = {
      value: front ? 1 :max,
      prev:()=>{
        --iter.value
      },
      next:()=>{
        ++iter.value
      },
      overflow:()=>{
        return iter.value<1 || iter.value>max
      },
      getTime:()=>{
        return new Time(this.year,this.month,iter.value)
      }
    }
    return iter
  }

  getPrevMonth() {
    if(this.month=== 1) {
      return new Time(this.year-1,12);
    } else{
      return new Time(this.year,this.month-1);
    }
  }
  getNextMonth() {
    if(this.month=== 12) {
      return new Time(this.year+1,1);
    } else{
      return new Time(this.year,this.month+1);
    }
  }

  getMonthFristDay() {
    return new Date(this.year,this.month-1).getDay();
  }

  getNextYear() {
    return new Time(this.year+1);
  }

  getPrevYear() {
    return new Time(this.year-1);
  }

  getNextYearRange() {
    const result = new Time()
    result.yearRange = this.yearRange.getNextYearRange()
    return result
  }

  getPrevYearRange() {
    const result = new Time()
    result.yearRange = this.yearRange.getPrevYearRange()
    return result
  }
}

class YearRange{
  constructor(begin,end) {
    this.begin = begin;
    this.end = end;
  }

  getPrevYearRange (yearRange) {
    return new YearRange(this.begin-10,this.end-10)
  }
  getNextYearRange (yearRange) {
    return new YearRange(this.begin+10,this.end+10)
  }

  static getYearRangeOfYear = (year) => {
    const begin = year - year%10
    const end = begin + 9
    return new this(begin, end)
  }
}

const generateKey = (time, {date=true,month=true,year=true}={date:true,month:true,year:true}) => {
  if(time instanceof Time) {
    const keyOfYearRange = `[${time.yearRange.begin}-${time.yearRange.end}]`
    const keyOfYear = year && g.notUndefined(time.year) ? `-${time.year}` : '';
    const keyOfMonth = month && g.notUndefined(time.month) ? `-${time.month}` : '';
    const keyOfDate = date && g.notUndefined(time.date) ? `-${time.date}` : '';

    return keyOfYearRange+keyOfYear+keyOfMonth+keyOfDate
    //return `${time.year}${month&&g.notUndefined(time.month)?`-${time.month}${date&&g.notUndefined(time.date)?`-${time.date}`:''}`:''}`
  } else {
    return `${time.begin}-${time.end}`
  }
  
}

class Clip{
  time
  data = []
  overlapPrev = 0
  overlapNext = 0
  count

  constructor(time) {
    this.time = time
  }

  prevDistance() {
    return this.count - this.overlapPrev;
  }

  nextDistance() {
    return this.count - this.overlapNext;
  }
}

class CalendarStore{
  year
  month
  date
  day
  datesClips = new Map()
  today

  focusMonth
  focusYear
  focusYearRange
  

  constructor() {
    const date = new Date();
    this.year = date.getFullYear()
    this.month = date.getMonth() + 1
    this.date = date.getDate()
    this.day = date.getDay()
    this.today = new Time(this.year,this.month,this.date)
    
    //this.focusMonth = this.today
    //this.focusYear = this.today
    this.focusYearRange = this.focusYear = this.focusMonth = this.today
  }

  getDatesClip(time) {
    const key = generateKey(time,{date:false});
  
    const clip = this.datesClips.get(key)

    if(!clip) {
      const fillClip = (month, front = true, size) => {
        const iter = month.getDatesIter(front);
        

        for(let l = 0; (size === undefined || l < size) && !iter.overflow();++l){

          if(front) {
            clip.data.push(iter.getTime())
            iter.next()
          } else {
            clip.data.unshift(iter.getTime())
            iter.prev()
          }
        }
      }

      const clip = new Clip(time);
      const firstDay = time.getMonthFristDay();
      const prevDateSize = firstDay === 0 ? 6 : firstDay - 1;
      if(prevDateSize) {
        clip.overlapPrev = 1
        const prevMonth = time.getPrevMonth()
        fillClip(prevMonth, false, prevDateSize)
      }
      fillClip(time);
      if(clip.data.length%7 !== 0) {
        clip.overlapNext = 1
        const nextMonth = time.getNextMonth()
        fillClip(nextMonth,true,7-clip.data.length%7)
      }

      clip.count = clip.data.length/7

      this.datesClips.set(key, clip);
      return clip;
    }

    return clip;
  }

  getMonthsClip(time) {
    const clip = new Clip(time)
    clip.count = 3
    for(let l = 1; l < 13; ++l) {
      clip.data.push(new Time(time.year, l))
    }
    return clip
  }

  getYearsClip(time) {
    const clip = new Clip(time)
    clip.count = 3

    let l = time.yearRange.begin
    for(; l <= time.yearRange.end;++l) {
      clip.data.push(new Time(l))
    }

    if(time.yearRange.begin % 20 === 0) {
      clip.overlapNext = 1

      clip.data.push(new Time(l+1))
      clip.data.push(new Time(l+2))
    } else {
      clip.overlapPrev = 1
    
      l = time.yearRange.begin 
      clip.data.unshift(new Time(l-1))
      clip.data.unshift(new Time(l-2))
    }
    return clip
  }
}

const SetDisplayPageContext = createContext()

export const ElCalendar = props => {

  const store = useRef(null);
  const getStore = () => {
    if(store.current === null) {
      store.current = new CalendarStore()
    }
    return store.current;
  }

  const {systemWindowsSub, windows11GlobalEventHandle} = useContext(ImmobilizationContext)
  const systemWindows = systemWindowsSub.get()
  const minimizedType = systemWindows.calendar.minimizedType
  const [minimized, setMinimized] = useState(systemWindows.calendar.minimized)

  const uiAttrs = {}
  if(minimized) {
    uiAttrs['ui-minimized'] = minimizedType
  }

  useEffect(() => {
    const linstener = val => {
      setMinimized(val.calendar.minimized)
    }
    systemWindowsSub.subscribe(linstener)
    return () => systemWindowsSub.unsubscribe(linstener)
  },[systemWindowsSub])

  const [displayPage, setDisplayPage] = useState('dates')

  const onPointerDown = e => {
    e.calendar = true
  }

  useEffect(() => {
    const gPointerDown = e => {
      if(!e.calendar) {
        systemWindowsSub.set(old => {
          return {...old, calendar:{
          ...old.calendar,
          minimized:true
        }}
      })}
    }
    windows11GlobalEventHandle.pointerdown.add(gPointerDown)
    return () => windows11GlobalEventHandle.pointerdown.delete(gPointerDown)
  },[systemWindowsSub,windows11GlobalEventHandle])
  
  return (
    <SetDisplayPageContext.Provider value={setDisplayPage}>
    <CalendarContext.Provider value={getStore()}>
      <div className='system-window calendar' onPointerDown={onPointerDown}  {...uiAttrs}>
        <Header/>
        <div className='main'>
          <ElDates displayPage={displayPage} setDisplayPage={setDisplayPage}/>
          <ElMonths displayPage={displayPage}/>
          <ElYears displayPage={displayPage}/>
        </div>
      </div>
    </CalendarContext.Provider>
    </SetDisplayPageContext.Provider>
  )
}

const Header = props=>{
  const store = useContext(CalendarContext)

  const dayToWeekDay = (date) => {
    const obj = {
      0:'日',
      1:'一',
      2:'二',
      3:'三',
      4:'四',
      5:'五',
      6:'六',
    }
    return '星期'+obj[date];
  }

  const weekDay = dayToWeekDay(store.day);
  const month = store.month;
  const date = store.date;

  return <div className='header'>
    <span>{`${month}月${date}日，${weekDay}`}</span>
  </div>
}

const useClips = (clipUtil, textCreator, thresholdUp, thresholdDown) => {

  const store = useContext(CalendarContext)

  const focus = clipUtil.getFocus(store)
  const prev1 = clipUtil.getPrev(focus)
  const next1 = clipUtil.getNext(focus)

  const focusClip = clipUtil.getClip(store,focus)
  const prev1Clip = clipUtil.getClip(store,prev1)
  const prev2Clip = clipUtil.getClip(store,clipUtil.getPrev(prev1))
  const next1Clip = clipUtil.getClip(store,next1)
  const next2Clip = clipUtil.getClip(store,clipUtil.getNext(next1))

  const text = textCreator(focus);

  const top = useRef(0)

  const update = useUpdate()

  const handleClickDown = () => {
    clipUtil.setFocus(store,next1)
    top.current = 0
    update()
  }

  const handleClickUp = () => {
    clipUtil.setFocus(store,prev1)
    top.current = 0
    update()
  }

  const scrollAble = useRef(false)
  const onPointerEnter=()=>{
    scrollAble.current = true
  }

  const onPointerLeave=()=>{
    scrollAble.current = false
  }

  const styleVarTops = [top.current-prev2Clip.nextDistance()-top.current-prev1Clip.nextDistance(),top.current-prev1Clip.nextDistance(),top.current,top.current+focusClip.nextDistance(),top.current+focusClip.nextDistance()+next1Clip.nextDistance()];

  useEffect(() => {
    if((focusClip.count + top.current - focusClip.overlapNext) < thresholdUp) {
      clipUtil.setFocus(store,next1)
      top.current = top.current + focusClip.nextDistance()
      update()
    }
    if(top.current >=thresholdDown) {
      clipUtil.setFocus(store,prev1)
      top.current = top.current - prev1Clip.nextDistance()
      update()
    }
  },[focusClip,prev1,next1,prev1Clip,store,update,thresholdDown,thresholdUp,clipUtil])

  useEffect(() => {
    const onWheel = e=> {
      if(scrollAble.current){
        if(e.deltaY > 0) {
          top.current = top.current - 1;
        } else {
          top.current = top.current + 1;
        }
        update()
      } 
    }
    return getEffect('wheel', onWheel)
  },[update])

  const handleClickText = () => {
    top.current = 0
    clipUtil.setParentFocus(store)
  }

  const onClick = (time) => {
    clipUtil.setClipFocus(store,time)
  }

  return {
    clips:[prev2Clip,prev1Clip,focusClip,next1Clip,next2Clip],
    styleVarTops,
    handles:{handleClickDown,handleClickUp,handleClickText},
    pointerEvent:{onPointerEnter,onPointerLeave},
    text,
    update,
    onClick
  }
}

const createClipUtil2 = (setDisplayPage,{focusName,clipName,parentName}) => {
  const capitalStartName = focusName[0].toUpperCase()+focusName.slice(1)
  const capitalStartClipName = clipName[0].toUpperCase()+clipName.slice(1)
  const capitalStartParentName = parentName ? parentName[0].toUpperCase()+parentName.slice(1) : ''

  const FocusName = `focus${capitalStartName}`
  const getClipFnName = `get${capitalStartClipName}sClip`
  const getNextFnName = `getNext${capitalStartName}`
  const getPrevFnName = `getPrev${capitalStartName}`

  return {
    getFocus: (store) => {
      return store[FocusName]
    },
    setFocus: (store,value) => {
      store[FocusName] = value
    },
    getClip:(store, time)=>{
      return store[getClipFnName](time)
    },
    getPrev:(time) => {
      return time[getPrevFnName]()
    },
    getNext:(time) => {
      return time[getNextFnName]()
    },
    setClipFocus: (store,value) => {
      if(clipName) {
        store[`focus${capitalStartClipName}`] = value
        setDisplayPage(`${clipName}s`)
      }
    },
    setParentFocus: (store) => {
      if(parentName) {
        store[`focus${capitalStartParentName}`] = store[FocusName]
        setDisplayPage(`${focusName}s`)
      }
    }
  }
}

const ElDates = props => {
  const setDisplayPage = useContext(SetDisplayPageContext)
  const clipUtil = useRef(createClipUtil2(setDisplayPage,{focusName:'month',clipName:'date',parentName:'year'}))
  const textCreator = (time) => {
    return `${time.year}年${time.month}月`
  }
  
  const {clips,styleVarTops,handles,pointerEvent,text,store} = useClips(clipUtil.current, textCreator,3,3)

  const type = props.displayPage === 'dates' ? 'normal' : 'shrink'

  return <div className='page dates' data-type={type}>
    <ElPageHeader {...handles} text={text}/>
    <ElWeekDays/>
    <div className='clips datas-clips' {...pointerEvent}>
      {clips.map((clip,index) => <ElDatesClip key={generateKey(clip.time,{date:false})} varTop={styleVarTops[index]} clip={clip}/>)}
    </div>
  </div>
}

const ElWeekDays = props => {
  return <div className='weekdays'>
    <ElWeekday name='一'/>
    <ElWeekday name='二'/>
    <ElWeekday name='三'/>
    <ElWeekday name='四'/>
    <ElWeekday name='五'/>
    <ElWeekday name='六'/>
    <ElWeekday name='日'/>
  </div>
}

const ElWeekday = props => {
  const name = props.name;
  return <div className='weekday'><span>{name}</span></div>
}

const ElDatesClip = props => {
  const {clip,varTop} = props

  const times = clip.data;

  const style = {
    '--count':clip.count,
    '--max-count':6,
    '--rows':7,
    '--top':varTop
  }

  return <div className='clip' style={style}>
    {times.map(time =>  <ElDate update={props.update} key={generateKey(time)} time={time}/>)}
  </div>
}

const ElDate = props => {
  const time = props.time;

  const store = useContext(CalendarContext)
  const focusMonth = store.focusMonth

  const today = store.today;

  const type = today.equalsDate(time) ? 'today' : focusMonth.equalsMonth(time) ? 'focus' : 'blur';

  return <div className='wrap'>
    <div className='btn' data-type={type} style={{
      '--size':'40px'
    }}>
      <span>
        {time.date}
      </span>
    </div>
  </div>
}

const ElMonths = props => {

  const setDisplayPage = useContext(SetDisplayPageContext)
  const clipUtil = useRef(createClipUtil2(setDisplayPage,{focusName:'year',clipName:'month', parentName:'yearRange'}))
  const textCreator = (time) => {
    return `${time.year}年`
  }
  const {clips,styleVarTops,handles,pointerEvent,text,store} = useClips(clipUtil.current,textCreator,2,2,'years')

  const displayPage = props.displayPage
  const type = displayPage === 'months' ? 'normal' : displayPage === 'years' ? 'shrink' : 'grow'

  return <div className='page months' data-type={type}>
    <ElPageHeader {...handles} text={text}/>
    <div className='clips datas-clips' {...pointerEvent}>
      {clips.map((clip,index) => <ElMonthsClip id={generateKey(clip.time,{date:false})} key={generateKey(clip.time,{date:false})} varTop={styleVarTops[index]} clip={clip}/>)}
    </div>
  </div>
}

const ElMonthsClip = props => {
  const {clip,varTop} = props

  const times = clip.data;

  const style = {
    '--count':clip.count,
    '--max-count':4,
    '--rows':4,
    '--top':varTop
  }

  return <div id={props.id} className='clip' style={style}>
    {times.map(time =>  <ElMonth key={generateKey(time)} time={time}/>)}
  </div>
}

const ElMonth = props => {
  const time = props.time;

  const store = useContext(CalendarContext)
  const focusYear = store.focusYear

  const today = store.today;

  const type = today.equalsMonth(time) ? 'today' : focusYear.equalsYear(time) ? 'focus' : 'blur';

  const setDisplayPage = useContext(SetDisplayPageContext);
  const onClick = () => {
    store.focusMonth = time
    setDisplayPage('dates')
  }

  return <div className='wrap'>
    <div className='btn' data-type={type} style={{
      '--size':'60px'
    }} onClick={onClick}>
      <span>
        {`${time.month}月`}
      </span>
    </div>
  </div>
}

const ElYears = props => {

  const setDisplayPage = useContext(SetDisplayPageContext)
  const clipUtil = useRef(createClipUtil2(setDisplayPage,{focusName:'yearRange',clipName:'year'}))
  
  const textCreator = (time) => {
    return `${time.yearRange.begin} - ${time.yearRange.end}`
  }

  const {clips,styleVarTops,handles,pointerEvent,text, update, onClick} = useClips(clipUtil.current,textCreator,2,2)

  const type = props.displayPage === 'years' ? 'normal' : 'grow'

  return <div className='page years' data-type={type}>
  <ElPageHeader {...handles} text={text}/>
  <div className='clips datas-clips' {...pointerEvent}>
    {clips.map((clip,index) => <ElYearsClip update={update} id={generateKey(clip.time,{date:false})} key={generateKey(clip.time,{date:false})} varTop={styleVarTops[index]} clip={clip} onClick={onClick}/>)}
  </div>
</div>
}

const ElYearsClip = props => {
  const {clip,varTop} = props

  const times = clip.data;
  
  const style = {
    '--count':clip.count,
    '--max-count':4,
    '--rows':4,
    '--top':varTop
  }

  return <div id={props.id} className='clip' style={style}>
  {times.map(time =>  <ElYear update={props.update} key={generateKey(time)} time={time} onClick={props.onClick}/>)}
</div>
}

const ElYear = props => {
  const time = props.time;

  const store = useContext(CalendarContext)
  const today = store.today
  const focusYearRange = store.focusYearRange

  const type = today.equalsYear(time) ? 'today' : time.inYearRange(focusYearRange.yearRange) ? 'focus' : 'blur';

  const setDisplayPage = useContext(SetDisplayPageContext);
  const onClick = () => {
    store.focusYear = time
    setDisplayPage('months')
  }

  return <div className='wrap'>
    <div className='btn' data-type={type} style={{
      '--size':'60px'
    }} onClick={onClick}>
      <span>
        {`${time.year}`}
      </span>
    </div>
  </div>
}

const ElPageHeader = props => {
  const {handleClickText, handleClickUp, handleClickDown, text} = props;

  return <div className='header'>
    <div className='text-btn' onClick={handleClickText?handleClickText:()=>{}}>
    <span>{text}</span>
    </div>
    <div className='up' onClick={handleClickUp}><ArrowUp/></div>
    <div className='down' onClick={handleClickDown}><ArrowDown/></div>
  </div>
}
