import './Arrow.sass'

export const ArrowUp = props => {
  const {className, ...other} = props;

  const thisClass = `arrow arrow-up${className ? ` ${className}`:''}`

  return <div className={thisClass} {...other}></div>
}

export  const ArrowDown = props => {
  const {className, ...other} = props

  const thisClass = `arrow arrow-down${className ? ` ${className}`:''}`

  return <div className={thisClass} {...other}></div>
}