import { React, useContext, useEffect, useRef, useState } from 'react';
import Draggable from '../utils/Draggable';
import { g } from '../utils/Function';
import { useImmobilization } from '../utils/Hooks';
import { Resizeable } from '../utils/Resizeable';
import { ImmobilizationContext } from '../Windows11';
import './Window.sass';

const getStyle = (style) => {
  const defaultStyle = {
    height:700,
    width:600,
    left:100,
    top:100,
    minWidth:200,
    minHeight:200,
  }

  return g.notUndefined(style) ? {...defaultStyle, ...style} : defaultStyle
}

export const Window = props => {

  const {pid} = props

  const windowRef = useRef()

  const {windowsSub} = useContext(ImmobilizationContext)

  const windows = windowsSub.get()

  const thisWindow = windows[pid]

  const {title,resizeable, icon} = thisWindow

  const [style,setStyle] = useState(() => getStyle(thisWindow.style))
  const [minimized, setMinimized] = useState(thisWindow.minimized)
  const [maximized, setMaximized] = useState(thisWindow.maximizad)

  useEffect(() => {
    const linstener = val => {
      const thisWindow = val[pid]
      if(thisWindow) {
        if(!g.deepEquals(thisWindow.style.zIndex, style.zIndex)) {
          setStyle({...style,zIndex:thisWindow.style.zIndex})
        }
        if(thisWindow.minimized !== minimized) {
          setMinimized(thisWindow.minimized)
        }
        if(thisWindow.maximized !== maximized) {
          setMaximized(thisWindow.maximized)
        }
      }
    }

    windowsSub.subscribe(linstener)

    return () => windowsSub.unsubscribe(linstener)
  },[style, maximized, minimized, pid, windowsSub])

  useEffect(() => {
    if(minimized) {
      setStyle(s => {
        let translateX, translateY
        if(maximized) {
          translateX = g.toPx(g.eid(`taskbar-app-btn-${pid}`).offsetLeft + 20)
          translateY = `-12px`
        } else {
          translateX = g.toPx(g.eid(`taskbar-app-btn-${pid}`).offsetLeft + 20 - s.left)
          translateY = `calc(100vh - 60px - ${g.toPx(s.top + s.height)})`
        }
        return {
          ...s,
          '--translatex': translateX,
          '--translatey': translateY
        }
      })
    }
  },[maximized,minimized,pid])

  const onClick = (e) => {
    e.cancelFocusOrBlur = true
  }

  const onPointerDown = e => {
    e.pid = pid
  }

  const [transition, setTransition] = useState(true)

  const onResizeStart = useImmobilization(() => setTransition(false))
  const onResizeEnd = useImmobilization(() => setTransition(true))

  return (
    <div key={pid} ref={windowRef} id={`window-${pid}`} className='window' style={style} onPointerDown={onPointerDown} onClick={onClick}
    ui-minimization={`${minimized}`} 
    ui-maximization={`${maximized}`}
    ui-transition={`${transition}`}
    >

      {title && <Titlebar pid={pid} setTransition={setTransition} maximization={maximized} title={title} icon={icon} style={style} setStyle={setStyle}></Titlebar>}

      {resizeable && <Resizeable setStyle={setStyle} targetRef={windowRef} minHeight={style.minHeight} minWidth={style.minWidth} size={10} resizeable={resizeable}
      onResizeStart={onResizeStart}
      onResizeEnd={onResizeEnd}
      />}

      <div className='content'>
        {props.render({
          pid,
          style,
          setWindowStyle:setStyle
        })}
      </div>

    </div>
  )
}

const Titlebar = props => {
  const {icon,pid,title,setStyle,style,maximization, setTransition} = props;

  /*
  const [dragValue,setDragValue] = useState({
    draging: false
  })

  useEffect(() => {
    const move = e => {
      if(dragValue.draging) {
        const dx = e.clientX - dragValue.bx;
        const dy = e.clientY - dragValue.by;
        const top = dragValue.bt + dy >= 0 ? dragValue.bt + dy : 0;
        const left = dragValue.bl + dx;
        if(top !== props.style.top || left !== props.style.left) {
          props.setStyle({
            ...props.style,
            top: top,
            left: left
          })
        }
      }
    }

    const end = () => {
      if(dragValue.draging) {
        setDragValue({draging: false});
        console.log('drag end');
      }
    }

    return getEffect('pointermove', move, 'pointerup', end);
  },[dragValue, props])
  */

  const {minimize, maximize, unmaximize, close} = useContext(ImmobilizationContext)

  const onMinimize = (e) => {
    e.cancelFocusOrBlur = true
    minimize(pid)
  }

  const onMaximize = (e) => {
    e.cancelFocusOrBlur = true
    if(maximization) {
      unmaximize(pid)
    } else {
      maximize(pid)
    }
  }

  const onClose = () => {
    close(pid)
  }

  const bl = useRef(),bt=useRef();

  const handleDragStart = () => {
    bl.current = style.left;
    bt.current = style.top;
    setTransition(false)
  }

  const handleDrag = ({dx,dy,bx,by,ex,eu}) => {
    setStyle(style=>{
      return {
        ...style,
        left:bl.current + dx,
        top: ((bt.current+dy) > 0 ? (bt.current+dy) : 0)
      }
    })
  }

  const handleDragEnd = () => {
    setTransition(true)
  }

  return (
    <div className='titlebar select-none'>
      <div className='titlebar-css relative over-hidden flex align-c wh-100'>
        <Draggable className='leftbar' 
        onDragStart={handleDragStart}
        onDrag={handleDrag} 
        onDragEnd={handleDragEnd}
        >
          <div className='icon'>{icon}</div>
          <span className='title'>{title}</span>
        </Draggable>
        <div className='btn' ui-type='minimize' onClick={onMinimize}></div>
        <div className='btn' ui-type='maximize' onClick={onMaximize}></div>
        <div className='btn' ui-type='close'  onClick={onClose}></div>
      </div>
    </div>
  )
}