import { createContext, useEffect, useRef, useState } from 'react'
import { renderSnakeGame } from './AppWindows/SnakeGame/SnakeGame'
import { Icon } from './Base/Icon'
import { ElCalendar } from './Calendar/Calendar2'
import { Desktop } from './Desktop/Desktop'
import { StartMenu } from './StartMenu/StartMenu'
import { Taskbar } from './Taskbar/Taskbar'
import { renderTaskbar } from './AppWindows/Taskmgr/Taskmgr'
import { getEffect } from './utils/GlobalEventHandle'
import { useImmobilization, useSubscribe, useUpdate } from './utils/Hooks'
import { Window } from './Window/Window'
import './Windows11.sass'
import './public.css'

export const ImmobilizationContext = createContext()
export const ProcessContext = createContext()

export const Windows11 = props => {

  const apps = useImmobilization(() => {
    const apps = config.apps
    for(let props of Object.values(apps)) {
      props.processCount = 0
    }
    return apps
  }, true)
  const [process, setProcess] = useState({})
  const windowsSub = useSubscribe({})

  const update = useUpdate()
  
  const focusPid = useRef(null)

  const run = useImmobilization((aid) => {
    const app = apps[aid]
    const windows = windowsSub.get()
    if(!app.single || app.processCount === 0) {
      const pid = app.single ? aid : `${aid}-${Date.now()}-${Math.random()}`
      windows[pid] = {
        minimized: false,
        focus: false,
        maximized: false,
        icon: app.getIcon(),
        ...app.window
      }

      const defaultStyle = {
        height:700,
        width:600,
        left:100,
        top:100,
        minWidth:200,
        minHeight:200,
        zIndex:1
      }

      if(app.window.style) {
        windows[pid].style = {...defaultStyle,...app.window.style}
      } else {
        windows[pid].style = defaultStyle
      }

      app.processCount+=1
      
      focus(pid)

      setProcess(old => {
        old[pid] = aid
        return {...old}
      }) 
    } else {
      if(windows[aid].minimized) {
        unminimize(aid)
      } else {
        focus(aid)
      }
    }
  })

  const close = useImmobilization((pid) => {
    const windows = windowsSub.get()
    
    if(focusPid.current === pid) {
      focusPid.current = null
    }
    setProcess(old => {
      apps[old[pid]].processCount -= 1
      delete old[pid]
      return {...old}
    })
    delete windows[pid]
    windowsSub.set({...windows})
  })

  const focusZindex = useRef(1)

  const focus = useImmobilization((pid) => {
    const windows = windowsSub.get()

    if(!windows[pid].focus) {

      if(focusPid.current !== null) {
        windows[focusPid.current].focus = false
      }

      windows[pid].focus = true
      windows[pid].style.zIndex = focusZindex.current

      focusPid.current = pid
      focusZindex.current += 1

      windowsSub.set({...windows})
    }
  })

  const blur = useImmobilization((pid) => {
    const windows = windowsSub.get()

    if(windows[pid].focus) {
      focusPid.current = null
      windows[pid].focus = false
      
      windowsSub.set({...windows})
    }
  })

  const minimize = useImmobilization((pid) => {
    const windows = windowsSub.get()

    windows[pid].minimized = true
    blur(pid)
  })

  const unminimize = useImmobilization((pid) => {
    const windows = windowsSub.get()

    windows[pid].minimized = false
    focus(pid)
  })

  const maximize = useImmobilization((pid) => {
    const windows = windowsSub.get()

    if(!windows[pid].maximized) {
      windows[pid].maximized = true
      
      windowsSub.set({...windows})
    }
  })

  const unmaximize = useImmobilization((pid) => {
    const windows = windowsSub.get()
    if(windows[pid].maximized) {
      windows[pid].maximized = false
      
      windowsSub.set({...windows})
    }
  })

  const systemWindowsSub = useSubscribe(config.systemWindows)

  const windows11GlobalEventHandle = useImmobilization({
    pointerdown: new Set(),
    pointerup: new Set()
  })

  const immobilization = useRef({
    apps,windowsSub,systemWindowsSub,windows11GlobalEventHandle,
    run,close,focus,blur,minimize,unminimize,maximize,unmaximize,
    gUpdate:update,
  })

  const blurAll = useImmobilization(() => {
    const windows = windowsSub.get()

    if(focusPid.current !== null) {
      if(windows[focusPid.current]) {
        windows[focusPid.current].focus = false
      }
      focusPid.current = null

      windowsSub.set({...windows})
    }
  })

  useEffect(() => {
    const onClick = e => {
      
    }
    return getEffect('click',onClick)
  })

  const onClick = e => {
    if(e.cancelFocusOrBlur) {
      return
    } else {
      if(e.pid) {
        focus(e.pid)
      } else {
        blurAll()
      }
    }
  }

  const onPointerDown = e => {
    if(e.pid) {
      focus(e.pid)
    }
    for(let handle of windows11GlobalEventHandle.pointerdown) {
      handle(e)
    }
  }

  const onPointerUp = e => {
    for(let handle of windows11GlobalEventHandle.pointerup) {
      handle(e)
    }
  }

  const windows = windowsSub.get()

  return (
    <ImmobilizationContext.Provider value={immobilization.current}>
    
      <div className='windows11' onClick={onClick} onPointerDown={onPointerDown} onPointerUp={onPointerUp}>

        <ProcessContext.Provider value={process}>
          <Taskbar/>
        </ProcessContext.Provider>

        <div className='css-wrap-1'>
          <div className='css-wrap-2'>
            <Desktop/>

            <ProcessContext.Provider value={process}>

              {
                /**normal windows */
                Object.keys(process).map(pid => {
                  const render = windows[pid].render
                  return <Window key={pid} pid={pid} render={render}/>
                })
              }

            </ProcessContext.Provider>
          </div>

          {
            /**system windows */
          }
          <StartMenu/>
          <ElCalendar/>

        </div>
      </div>
    </ImmobilizationContext.Provider>
  )
}

