import { useContext, useEffect, useRef, useState } from 'react';
import DownAndGlobalUp from '../utils/DownAndGlobalUp';
import Draggable from '../utils/Draggable';
import { g } from '../utils/Function';
import { getEffect } from '../utils/GlobalEventHandle';
import { useImmobilization, useUpdate } from '../utils/Hooks';
import { ImmobilizationContext, ProcessContext } from '../Windows11';
import { ElTimeButton } from './ElTimeButton';
import  './Taskbar.sass';

export function StartMenuIcon(props) {

  const svgId = useRef(`clip-svg-${Date.now()}`);

  return (
    <DownAndGlobalUp className={`basic start flex-c ${props.pointer}`}>
      <div className='main flex-c relative'>
        <div className='icon' style={{clipPath: `url('#${svgId.current}')`}}></div>
        <svg className='absolute start-clip-svg'>
          <clipPath id={svgId.current}>
            <rect x='0' y='0' width='11' height='11'/>
            <rect x='12' y='0' width='11' height='11'/>
            <rect x='0' y='12' width='11' height='11'/>
            <rect x='12' y='12' width='11' height='11'/>
          </clipPath>
        </svg>
      </div>
    </DownAndGlobalUp>
  )
}

export function AppIcon(props) {

  const init = useRef(true);
  const [pointer, setPointer] = useState('none');

  const [style, setStyle] = useState({});

  const prevIndex = useRef(props.index);
  const translateX = useRef(0);

  const width = useRef(44);

  useEffect(() => {
    if(props.index !== prevIndex.current && style.translateX) {
      translateX.current -= (props.index - prevIndex) * width.current;
      prevIndex.current = props.index;
      setStyle({transform:`translateX(${g.toPx(translateX.current)})`})
    }
  }, [props.index, style.translateX])

  function handleDragStart() {
    setPointer('drag');
  }

  function handleDrag({dx}) {
    const min = -props.index * width.current;
    const max = (props.size - 1 - props.index) * width.current;
    translateX.current = dx < min ? min : dx > max ? max : dx;
    setStyle({transform:`translateX(${g.toPx(translateX.current)})`})
  }

  function handleDragEnd() {
    setStyle({})
    setPointer('none')
  }

  function handlePDown() {
    setPointer('down');
  }

  useEffect(() => {
    function handleGPUp() {
      if(pointer !== 'none') {
        setPointer('none');
      }
    }

    return getEffect('pointerup', handleGPUp);
  },[pointer])

  const displayThumbnail = props.displayThumbnail;

  return (
    <div className={`basic appicon select-none relative ${pointer} ${init.current ? 'init': props.minimized ? 'minimized':'unminimized'}${props.focused ? ' focused':''}`} style={style}
    >
      <Draggable threshold={5} onDragStart={handleDragStart} onDrag={handleDrag} onDragEnd={handleDragEnd} className='w-100 h-100 flex-c'
      onPointerDown={handlePDown}
      >
        <div className='main flex-c relative'>
          <img className='icon' src={props.img} alt='HHHHHHH'></img>
          <div className='pointer absolute'></div>
        </div>
      </Draggable>
      {
        displayThumbnail && <ThumbnailBar {...props.thumbnail}/>
      }
    </div>
  )
}

