export function window2img(window) {
  const img = new Image();

  const clone = window.cloneNode();
  clone.style.transform = '';
  //clone.style.transform.replace(/translate\([^)]+\)/g,'').replace(/translateX\([^)]+\)/g,'').replace(/translateY\([^)]+\)/g,'').replace(/scale\([^)]+\)/g,'').replace(/scaleX\([^)]+\)/g,'');

  const src = `data:image/svg+xml;charset=utf-8,<svg width='${window.offsetWidth}' height='${window.offsetHeight}' xmlns="http://www.w3.org/2000/svg"><foreignObject x="0" y="0" width="100%" height="100%">${new XMLSerializer().serializeToString(clone)}${[...document.querySelectorAll('style')].reduce((prev,current)=>prev+current.outerHTML,'')}</foreignObject></svg>`.replace(/\n/g,'').replace(/\t/g,'').replace(/#/g,'%23');

  img.src = src;
  return img;
}

function dom2img(dom) {
  const img = new Image();
  
  const clone = dom.cloneNode();
  const src = `data:image/svg+xml;charset=utf-8,<svg width='${dom.offsetWidth}' height='${dom.offsetHeight}' xmlns="http://www.w3.org/2000/svg"><foreignObject x="0" y="0" width="100%" height="100%">${new XMLSerializer().serializeToString(clone)}${[...document.querySelectorAll('style')].reduce((prev,current)=>prev+current.outerHTML,'')}</foreignObject></svg>`.replace(/\n/g,'').replace(/\t/g,'').replace(/#/g,'%23');

  img.src = src;
  return img;
}

function windowToCanvas(window,{width,height}) {
  console.log(`${width} ${height}`)

  const canvas = document.createElement('canvas');
  const context = canvas.getContext('2d');

  const img = new Image();


  img.onload = () => {
    draw(img); 
    document.body.appendChild(canvas);
  }


  function draw(img) {
    const width = img.width, height = img.height;
    
    canvas.width = width;
    canvas.height = height;
    context.clearRect(0,0,width, height);
    context.drawImage(img,0,0);
  }

  const cloneDom = window.cloneNode(true);
  const imgs = cloneDom.querySelectorAll('img');
  for(let img of imgs) {
    draw(img);
    img.src = context.toDataURL();
  }

  const div = document.createElement('div');
  div.appendChild(cloneDom)

  
  let svgHtml = `data:image/svg+xml;charset=utf-8,<svg width='${window.offsetWidth}' height='${window.offsetHeight}' xmlns="http://www.w3.org/2000/svg"><foreignObject x="0" y="0" width="100%" height="100%">${new XMLSerializer().serializeToString(cloneDom)}${[...document.querySelectorAll('style')].reduce((prev,current)=>prev+current.outerHTML,'')}</foreignObject></svg>`;
  svgHtml = svgHtml.replace(/\n/g,'').replace(/\t/g,'').replace(/#/g,'%23');
  

  /*
  let svgHtml = `data:image/svg+xml;charset=utf-8,<svg viewBox='0 0 ${window.offsetWidth} ${window.offsetHeight}' xmlns="http://www.w3.org/2000/svg"><foreignObject x="0" y="0" width="100%" height="100%">${new XMLSerializer().serializeToString(cloneDom)}${[...document.querySelectorAll('style')].reduce((prev,current)=>prev+current.outerHTML,'')}</foreignObject></svg>`;
  svgHtml = svgHtml.replace(/\n/g,'').replace(/\t/g,'').replace(/#/g,'%23');
  */

  img.src = svgHtml;
  return img;
}

