const StartMenuButton = (props) => {

  const clipId = useRef(`clip-${Date.now()}-${Math.random()}`);

  const [pointer, setPointer] = useState('none')
  const {systemWindowsSub} = useContext(ImmobilizationContext)
  const startWindow = systemWindowsSub.get().start
  const [focus, setFocus] = useState(!startWindow.minimized)

  useEffect(() => {
    const linstener = (val) => {
      setFocus(!systemWindowsSub.get().start.minimized)
    }
    systemWindowsSub.subscribe(linstener)
    return () => systemWindowsSub.unsubscribe(linstener)
  },[systemWindowsSub])

  const doClickRef = useRef(startWindow.minimized)

  const onClick = e => {
    if(doClickRef.current) {
      systemWindowsSub.set(old => {
        return {...old, start: {
          ...old.start,
          minimized:!true
        }}
      })
    }
  }

  const onPointerDown = e => {
    setPointer('down')
    doClickRef.current = startWindow.minimized
  }

  const onGlobalUp = useImmobilization(e => {
    setPointer('none')
  })


  return <DownAndGlobalUp onGlobalUp={onGlobalUp}>
    <LeftButton {...props} ui-pointer={pointer} ui-focus={focus.toString()} onClick={onClick} onPointerDown={onPointerDown} className='start-menu'>
      <div className='icon' style={{clipPath: `url('#${clipId.current}')`}}></div>
        <svg className='clip'>
          <clipPath id={clipId.current}>
            <rect x='0' y='0' width='11' height='11'/>
            <rect x='12' y='0' width='11' height='11'/>
            <rect x='0' y='12' width='11' height='11'/>
            <rect x='12' y='12' width='11' height='11'/>
          </clipPath>
        </svg>
    </LeftButton>
  </DownAndGlobalUp>
}

const InputButton = props => {
  const [text, setText] = useState('中')
  const onClick = () => {
    if(text==='中') {
      setText('英')
    } else {
      setText('中')
    }
  }

  return (
    <RightButton onClick={onClick}>
      <div>
        <span>{text}</span>
      </div>
    </RightButton>
  )
}

const AppButtonBar = props => {
  const {apps} = useContext(ImmobilizationContext)
  const process = useContext(ProcessContext)

  const update = useUpdate()
  const appBtns = useRef([])

  const getBtn = (pid) => {
    const aid = process[pid]
    const app = apps[aid]
    const icon = app.getIcon()
    return {pid, icon}
  }

  const btns = Object.keys(process).map(pid=>getBtn(pid))

  const [btnSort,setBtnSort] = useState(() => {
    const pid2index = new Map()
    const index2pid = new Map()
    for(let l = 0; l < btns.length;++l) {
      const index = l
      pid2index.set(btns[l].pid,index)
      index2pid.set(index,btns[l].pid)
    }
    return {pid2index, index2pid}
  })

  useEffect(() => {
    if(btns.length !== btnSort.pid2index.size) {
      if(btns.length > btnSort.pid2index.size) {
        for(let btn of btns) {
          if(!btnSort.pid2index.has(btn.pid)) {
            const index = btnSort.pid2index.size
            btnSort.pid2index.set(btn.pid, index)
            btnSort.index2pid.set(index, btn.pid)
          }
        }
      } else {
        const tmpSet = new Set(btns.map(btn => btn.pid))
        for(let [pid,index] of btnSort.pid2index.entries()) {
          if(!tmpSet.has(pid)) {
            btnSort.pid2index.delete(pid)
            for(let l = index; l < btnSort.index2pid.size - 1;++l) {
              const pid = btnSort.index2pid.get(index+1)
              btnSort.index2pid.set(index, pid)
              btnSort.pid2index.set(pid, index)
            }
            btnSort.index2pid.delete(btnSort.index2pid.size - 1)
          }
        }
      }
      setBtnSort({...btnSort})
    }
  },[btns, btnSort])

  const switchIndex = (index1, index2) => {
    const index2pid = btnSort.index2pid
    const pid2index = btnSort.pid2index
    const pid1 = index2pid.get(index1)
    const pid2 = index2pid.get(index2)
    index2pid.set(index1, pid2)
    index2pid.set(index2, pid1)
    pid2index.set(pid1, index2)
    pid2index.set(pid2, index1)
    setBtnSort({index2pid, pid2index})
  }

  return <div className='app-btn-bar'>
    {
      btns.map((btn, defaultIndex) => {
        let index = btnSort.pid2index.get(btn.pid)
        if(index === undefined) {
          index = defaultIndex
        }
        return <AppButton index={index} switchIndex={switchIndex} size={btns.length} key={btn.pid} {...btn}></AppButton>
      })
    }
  </div>
}

const AppButton = props => {
  const {icon,pid,index,size, switchIndex} = props

  const thumbnail = props.thumbnail

  const [pointer, setPointer] = useState('none')

  const {windowsSub, minimize,unminimize} = useContext(ImmobilizationContext)
  const windows = windowsSub.get()

  const [uiFocus, setUiFocus] = useState(windows[pid].focus)
  const [minimized, setMinimized] = useState(windows[pid].minimized)

  useEffect(() => {
    const linstener = (val) => {
      if(val[pid]) {
        if(val[pid].focus !== uiFocus) {
          setUiFocus(val[pid].focus)
        }
        if(val[pid].minimized !== minimized) {
          setMinimized(val[pid].minimized)
        }
      }
    }
    windowsSub.subscribe(linstener)

    return () => windowsSub.unsubscribe(linstener)
  },[pid, uiFocus, minimized, windowsSub])


  const onPointerDown = () => {
    setPointer('down')
  }

  const cancelClick = useRef(false)

  const onDragStart = () => {
    setPointer('drag')
    cancelClick.current = true
  }

  const [styleValue, setStyleValue] = useState({left: index*44, translateX:0})
  
  //const style = {left: index ? index*44 :0, transform: `translateX(${g.toPx(translateX)})`}
  const style = {
    left: styleValue.left,
    transform: `translateX(${g.toPx(styleValue.translateX)})`
  }

  const onDrag = ({dx}) => {
    const min = -index * 44, max = (size - 1 - index) * 44
    const realyDx = dx - (indexChangeRef.current * 44)
    const translateX = realyDx < min ? min : realyDx > max ? max : realyDx
    setStyleValue(s => {
      return {...s, translateX}
    })
    if(translateX >= 24) {
      switchIndex(index, index+1)
    } else if(translateX < -24) {
      switchIndex(index, index-1)
    }
  }

  const prevIndexRef = useRef(index)
  const indexChangeRef  = useRef(0)

  useEffect(() => {
    setStyleValue(s => {
      const prevIndex = prevIndexRef.current
      const isDraging = s.translateX !== 0
      if(isDraging) {
        indexChangeRef.current += (index - prevIndex)
      }
      prevIndexRef.current = index
      return {
        left: s.left + (index - prevIndex) *44,
        translateX: !isDraging ? 0 : s.translateX - (index - prevIndex) *44
      }
    })
  },[index])

  const onDragEnd = () => {
    indexChangeRef.current = 0
    setStyleValue(s => {
      return {...s, translateX:0}
    })
  }

  useEffect(() => {
    const onPointerUp = () => {
      setPointer('none')
    }
    return getEffect('pointerup', onPointerUp)
  },[])

  const onClick = (e) => {
    if(cancelClick.current) {
      e.cancelFocusOrBlur = true
      cancelClick.current = false
    } else {
      e.pid = pid
      if(windows[pid].minimized) {
        e.cancelFocusOrBlur = true
        unminimize(pid)
      } else {
        if(windows[pid].focus) {
          e.cancelFocusOrBlur = true
          minimize(pid)
        }
      }
    }
  }

  const handles = {onPointerDown,onDragStart,onDrag,onDragEnd,onClick}

  return <Draggable id={`taskbar-app-btn-${pid}`} ui-pointer={pointer} ui-minimization={`${minimized}`}  threshold={5} {...handles} className='app-btn' style={style}>
    <LeftButton ui-pointer={pointer} ui-focus={`${uiFocus}`} ui-outline='true'>
      <div className='img-icon'>{icon}</div>
    </LeftButton>
  </Draggable>
}

const LeftButton = props => {
  //const thumbnail = props.thumbnail;
  
  const {children, className, ...ohter} = props;

  const thisClass = `left-btn${className ? ` ${className}` :''}`

  return (
    <div className={thisClass} {...ohter}>
      <Button>{children}</Button>
    </div>
  )
}

export const RightButton = props => {
  const {className, className2, ...other} = props;
  const thisClass = `right-btn${className?` ${className}`:''}`
  return <div className={thisClass}><Button className={className2} {...other}/></div>
}

const Button = props => {
 // const {children, pointer, focus, outline} = props;
  const {children, className, ...other} = props;
  const thisClass = `btn${className?` ${className}`:''}`

  return (
    <div className={thisClass} {...other}>
      {children}
    </div>
  )
}

