import { createContext, useContext, useEffect, useRef, useState } from 'react';
import { g } from '../utils/Function'
import { useImmobilization } from '../utils/Hooks';
import { config, ImmobilizationContext } from '../Windows11';
import './StartMenu.css'

const MinimizeStartContext = createContext()
const minimizedContext = createContext()

export function StartMenu(props) {

  const {systemWindowsSub, windows11GlobalEventHandle} = useContext(ImmobilizationContext)
  const systemWindows = systemWindowsSub.get()
  const start = systemWindows.start
  const [minimized, setMinimized] = useState(start.minimized)

  const uiAttrs = {}
  if(minimized) {
    uiAttrs['ui-minimized'] = start.minimizedType
  }

  const onPointerDown = e => {
    e.start = true
  }

  useEffect(() => {
    const linstener = val => {
      setMinimized(val.start.minimized)
    }
    systemWindowsSub.subscribe(linstener)
    return () => systemWindowsSub.unsubscribe(linstener)
  },[systemWindowsSub])

  const minimizeStart = useImmobilization(() => systemWindowsSub.set(old => {
    return {...old, start:{
    ...old.start,
    minimized:true
  }}}))

  useEffect(() => {
    const gPointerDown = e => {
      if(!e.start) {
        minimizeStart()
      }
    }
    windows11GlobalEventHandle.pointerdown.add(gPointerDown)
    return () => windows11GlobalEventHandle.pointerdown.delete(gPointerDown)
  },[minimizeStart,systemWindowsSub,windows11GlobalEventHandle])

  return (
    <MinimizeStartContext.Provider value={minimizeStart}>
      <div className='system-window start' {...uiAttrs} onPointerDown={onPointerDown}>
        <SearchBar/>
        <minimizedContext.Provider value={minimized}>
          <AppButtonsBar/>
        </minimizedContext.Provider>
        <BottomBar/>
      </div>
    </MinimizeStartContext.Provider>
  )
}

export function SearchBar() {
  return (
    <div className='search-bar'>
      <div className='search'>
        <SerachIcon className='icon'/>
        <span className='placeholder'>在此键入以搜索</span>
      </div>
    </div>
  )
}

