const AppButtonsBar = (props) => {
  const [page, setPage] = useState(1)

  const minimized = useContext(minimizedContext)

  useEffect(() => {
    if(minimized) {
      setPage(1)
    }
  },[minimized])

  return (
    <div className='app-buttons-bar'>
      <div className='container' style={{transform: `translateX(${-50*(page-1)}%)`}}>
        <AppButtonsBarPage1 setPage={setPage}/>
        <AppButtonsBarPage2 setPage={setPage}/>
      </div>
    </div>
  )
}

const AppButtonsBarPage1 = ({setPage}) => {
  const btnsRef = useRef(config.start.page1)

  const toPage2 = () => {
    setPage(2)
  }

  return (
    <div className='page page1'>
      <AppButtonsBarTitle title={'已固定'} button={<Page1TitleButton onClick={toPage2} text='所有应用'/>}/>
      <div className='buttons'>
        {
          btnsRef.current.map(btn => <Page1Button icon={btn.icon} name={btn.name} aid={btn.aid} key={btn.aid}/>)
        }
      </div>
    </div>
  )
}

const AppButtonsBarPage2 = ({setPage}) => {
  const toPage1 = () => {
    setPage(1)
  }

  const btnsRef = useRef(config.start.page2)

  return (
    <div className='page page2'>
      <AppButtonsBarTitle title={'所用应用'} button={<Page2TitleButton onClick={toPage1} text='返回'/>}/>
      <div className='buttons'>
        {btnsRef.current.map(button => {
          return getPage2Button(button);
        })}
      </div>
    </div>
  )
}

const AppButtonsBarTitle = props => {
  const {title, button} = props;
  
  return (
    <div className='title-bar'>
      <span className='title'>
        {title}
      </span>
      {button}
    </div>
  )
}

const Page1TitleButton = props => {
  return <TitleButton className='page1' {...props}/>
}

const Page2TitleButton = props=>{
  return <TitleButton className='page2' {...props}/>
}

const TitleButton = props => {
  const {text, className, ...ohter} = props
  const thisClass = 'button ' + className
  return <span className={thisClass} {...ohter}>
    {text}
  </span>
}

const Page1Button = ({icon,name,aid})=>{
  //const {icon, name, ...other} = props;

  const {run} = useContext(ImmobilizationContext)
  const minimizeStart = useContext(MinimizeStartContext)

  const onClick = (e) => {
    if(aid) {
      minimizeStart()
      run(aid)
    }
  }
  
  return (
    <Button className='page1' onClick={onClick}>
      <div className='icon'>{icon}</div>
      <span className='name'>{name}</span>
    </Button>
  )
}

const Page2Button = props=>{
  const {children, className, level, type,...other} = props;
  const thisClass = `page2`;

  const style = {
    marginLeft: level*12
  }

  const attrs = {
    'data-type':type
  }

  return <Button className={thisClass} {...attrs} {...other} style={style}>
      {children}
    </Button>
}

const Tag = props=> {
  const {tag, childButtons, ...other} = props;

  return (
  <div className='tag'>
    <Page2Button type='tag' {...other}>
      <div className='icon'>
        <span>{tag}</span>
      </div>
    </Page2Button>
    <div className='children'>
      {childButtons && childButtons.map((button, index) => {
        return getPage2Button(button,0);
      })}
    </div>
  </div>
  )
}

const FileButton = props=>{
  const {icon,name,level,aid} = props;
  const {run} = useContext(ImmobilizationContext)
  const minimizeStart = useContext(MinimizeStartContext)
  const onClick = () => {
    if(aid) {
      run(aid)
      minimizeStart()
    }
  }

  return <Page2Button type='file' level={level} onClick={onClick}>
      <div className='icon'>
        {icon}
      </div>
      <span className='name'>{name}</span>
    </Page2Button>
}

const Folder = props=>{


  const {icon,name,childButtons,level, parentExpan} = props;

  const [expand, setExpand] = useState(false)

  const minimized = useContext(minimizedContext)

  useEffect(() => {
    if(minimized) {
      setExpand(false)
    }
  },[minimized])

  useEffect(() => {
    if(g.notUndefined(parentExpan) && !parentExpan) {
      setExpand(false)
    }
  },[parentExpan])

  const attrs = {
    'data-expand': expand
  }

  const onClick=() => {
    setExpand(!expand)
  }

  return (
    <div className='folder' {...attrs}>
      <Page2Button type='folder' level={level} onClick={onClick}>
        <div className='icon'>
          {icon}
        </div>
        <span className='name'>{name}</span>
        <div className='css' aria-hidden='true'></div>
      </Page2Button>
      <div className='children'>
        <div className='css'>
          {childButtons && childButtons.map((button, index) => {
            return getPage2Button(button,level+1, expand);
          })}
        </div>
      </div>
    </div>
  )
}

const getPage2Button = (button,level,parentExpan) => {
  const thisLevel = g.notUndefined(level) ? level : 0;

  switch(button.type) {
    case 'file':
      return <FileButton key={button.name} {...button} level={thisLevel}/>;
    case 'folder':
      return <Folder key={button.name} {...button} parentExpan={parentExpan} level={thisLevel}/>;
    case 'tag':
      return <Tag key={button.tag} {...button} level={thisLevel}/>
    default:
      throw new Error('page2 button type error');
  }
}

const BottomBar = (props) => {
  return (
    <div className='bottom-bar'>
      <UserButton/>
      <div className='fill' aria-hidden='true'></div>
      <BottomSystemButton>
        <SettingIcon/>
      </BottomSystemButton>
      <BottomSystemButton>
        <PowerIcon/>
      </BottomSystemButton>
    </div>
  )
}

const Button = (props) => {
  const {children, pointerState, className, ...other} = props;

  const thisClass = `button${g.notUndefined(className) ? ` ${className}`:''}`;

  const attrs = {
    'data-pointer-state':pointerState?pointerState:'none'
  }

  return (
    <div className={thisClass} {...attrs} {...other}>
      {props.children}
    </div>
  )
}

const UserButton = props=>{

  return (
    <Button className='user' {...props}>
      <UserIcon/>
      <span>Administrator</span>
    </Button>
  )
}

