import { useState,useEffect, useContext } from 'react'
import { useImmobilization } from '../utils/Hooks'
import { ImmobilizationContext } from '../Windows11'
import { RightButton } from './Taskbar'

export const ElTimeButton = () => {

  const getTime = useImmobilization(() => {
    const dateObj = new Date()
    let minutes = dateObj.getMinutes()
    if(minutes < 10) {
      minutes = `0${minutes}`
    }
    return {
      hours: dateObj.getHours(),
      minutes,
      year: dateObj.getFullYear(),
      month: dateObj.getMonth()+1,
      date: dateObj.getDate()
    }
  })

  const [time, setTime] = useState(() => getTime())

  const text1 = `${time.hours}:${time.minutes}`
  const text2 = `${time.year}/${time.month}/${time.date}`

  const {systemWindowsSub} = useContext(ImmobilizationContext)

  const onClick = e => {
    const oldValue = systemWindowsSub.get().calendar.minimized
    systemWindowsSub.set(old => {
      return {...old, calendar: {
        ...old.calendar,
        minimized:!oldValue
      }}
    })
  }

  const onPointerDown = e=> {
    e.calendar = true
  }

  useEffect(() => {
    const tick = () => {
      setTime(getTime)
    }
    const intervalId = setInterval(tick, 1000)

    return () => clearInterval(intervalId)
  },[getTime])

  return <RightButton className2='time' onPointerDown={onPointerDown} onClick={onClick}>
    <span>{text1}</span>
    <span>{text2}</span>
  </RightButton>
}