import Vector from './Vector';

export const GlobalEventHandle = {
  evnetHandles:{
    pointerdown:new Vector(256),
    pointerup:new Vector(256),
    pointermove:new Vector(256),
    pointerleave:new Vector(256),
    resize:new Vector(256),
    keydown:new Vector(256),
    keyup:new Vector(256),
    wheel:new Vector(256),
    click:new Vector(256),
  },
  add:(type,listener) => {
    GlobalEventHandle.evnetHandles[type].push(listener);
  },
  remove:(type, listener) => {
    GlobalEventHandle.evnetHandles[type].deleteByValue(listener);
  }
}


function init() {
  for(let [key, value] of Object.entries(GlobalEventHandle.evnetHandles)) {
    window.addEventListener(key, e => {
      for(let listener of value) {
        listener(e);
      }
    });
  }
}

init();

export function getEffect(...args) {
  const returnFuncs = [];
  for(let l = 0; l < args.length; l+=2) {
    GlobalEventHandle.add(args[l], args[l+1]);
    returnFuncs.push(() => GlobalEventHandle.remove(args[l], args[l+1]));
  }
  return returnFuncs;
}